
import Vue from 'vue';
import Vuex from 'vuex';
// import mutations from './mutations';
// import actions from './actions';
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    userMsg: {}, // 
    userId: 1, // ID
    typeId: 1, //  123,456
    courseNum: 0, // 
    versionId: 1, // id
    textbookId: 1, // id
    unitId: 1, // id
    versionBoxTitle: '', // +
    unitBoxTitle: '', // 
    deviceBoxTitle: '', // 
    unitList: [], // 
    showGoStudyCenter: false, // 
  },
  mutations: {
    // 
    updateUserMsg(state, userMsg_) {
      state.userMsg = userMsg_;
      // console.log('store: ' + state.userMsg);
    },
    // ID
    updateUserId(state, userid_) {
      state.userId = userid_;
      // console.log('storeID: ' + state.userId);
    },
    // 
    updateTypeId(state, val_) {
      state.typeId = val_;
      // console.log('store: ' + state.typeId);
    },
    // 
    updateVersionBoxTitle(state, val_) {
      state.versionBoxTitle = val_;
      // console.log('store: ' + state.versionBoxTitle);
    },
    // 
    updateUnitBoxTitle(state, val_) {
      state.unitBoxTitle = val_;
      // console.log('store: ' + state.unitBoxTitle);
    },
    // 
    updateDeviceBoxTitle(state, val_) {
      state.deviceBoxTitle = val_;
      // console.log('store: ' + state.deviceBoxTitle);
    },
    // ID
    updateVersionId(state, val_) {
      state.versionId = val_;
      // console.log('storeID: ' + state.versionId);
    },
    // ID
    updateTextbookId(state, val_) {
      state.textbookId = val_;
      console.log('storeID: ' + state.textbookId);
    },
    // ID
    updateUnitId(state, val_) {
      state.unitId = val_;
      console.log('storeID: ' + state.unitId);
    },
    // 
    updateCourseNum(state, val_) {
      state.courseNum = val_;
      // console.log('store: ' + state.courseNum);
    },
    // 
    updateUnitList(state, arr_) {
      state.unitList = arr_;
      // console.log('store');
    },
    // 
    updateShowGoStudyCenter(state, stateType_) {
      if (stateType_) {
        state.showGoStudyCenter = true;
      }else{
        state.showGoStudyCenter = !state.showGoStudyCenter;
      }
    }
  },
  actions: {

  }
});

export default store;



