<template>
    <div id="wordWrite">
      <!---->
      <div class="topInfo">
        <!--<span>00:20:40</span>-->
        <!--<span>00:34:40</span>-->
        <!--<span>13%</span>-->
      </div>
      <!---->
      <div class="centerBox" v-loading="loading">
        <!---->
        <div class="nameBox">
          <div class="deviceName">{{thisTitle}}</div>
          <div class="version">{{thisVersionName}}</div>
          <div class="rightBtn clearfix">
            <!--<span class="help"></span>-->
            <!--<span class="skin"></span>-->
            <span class="close" @click="fnclosePage()"></span>
          </div>
        </div>
        <!---->
        <div class="mainCon">
          <!---->
          <div class="mean">{{thisWord.word_mean}}</div>
          <!---->
          <div class="inputBox" :class="{wrong:thisWordState === 2 , right:thisWordState === 1}">
            <div class="laba"></div>
            <input type="text" v-model="inputStr" autofocus id="thisInput"
                   v-focus="isfocus"
                   :disabled="isdisabled"
                   oncopy="return false;"
                   onpaste="return false;"
                   oncut="return false;"/>
            <div class="isRight"></div>
          </div>
          <!---->
          <div class="totalBar" v-show="isTest===1" :title="' '+ thisWord.dictation_percent +'%'">
            <div class="curBar" :style="{width: thisWord.dictation_percent + '%'}"></div>
          </div>
          <!---->
          <div class="wordBox">
            <div v-show="showWord">
              <span class="word">{{thisWord.word_name.replace(/\/g, '')}}</span>
              <span class="soundmark">{{thisWord.phonogram}}</span>
            </div>
          </div>
        </div>
        <!---->
        <div class="botInfo">
          <!--<span> 6</span>-->
          <span> {{studyTime}}</span>
          <span v-if="isTest === 1"> [  {{newWordNum}}{{oldWordNum}}{{reviewWordNum}} ]</span>
          <span v-else>{{thisWordIndex+1}}/{{wordList.length}}</span>
        </div>
        <audio src="" id="audio" autoplay="autoplay"></audio>
      </div>
    </div>
</template>

<script>
    export default {
        name: 'word-write',
        components: {},
        data() {
            return {
              loading: false,
              userMsg: {},
              unitId: 0,
              studyTime: '00:00:00', // 
              thisWord: {
                'id': 15,
                'word_name': 'ball',
                'word_mean': 'n.',
                'word_url': '\\UploadeFile\\Video\\ball.wav',
                'phonogram': '[b:l]'
              }, // 
              wordList: [],
              thisWordIndex: 0, // index
              thisWordState: 0, // 0   1   2 
              theWordNewOrOld: 0, //  0   0
              theWordState: 1, //  1  1
              inputStr: '', // input
              isfocus: true, // 
              isdisabled: false, // input
              newWordNum: 0, // 
              oldWordNum: 0, // 
              reviewWordNum: 0, // 
              showWord: false, // 
              thisTitle: '',
              isTest: 1, //  0:  1:  2:
              countTest: 1, //  1  2
              testArr: [], // 
              score: 0, // 
              againEnter: 2, // enter2-1
            }
        },
        methods: {
          fnclosePage() {
            this.$router.go(-1);
          },
          // 
          fnStudyTime() {
            let time = 0;
            setInterval(() => {
              time++;
              let hour = parseInt(time / 3600) < 10 ? '0' + parseInt(time / 3600) : parseInt(time / 3600);
              let minute = parseInt(time % 3600 / 60) < 10 ? '0' + parseInt(time % 3600 / 60) : parseInt(time % 3600 / 60);
              let second = parseInt(time % 60) < 10 ? '0' + parseInt(time % 60) : parseInt(time % 60);
              this.studyTime = hour + ':' + minute + ':' + second;
            }, 1000)
          },
          // 
          fnAudioPalyer(url_) {
            let audio = document.querySelector('#audio');
            audio.src = this.$url.url2 + url_;
          },
          // 
          fnGetWord() {
            this.loading = true;
            this.$ajax({
              method: 'GET',
              url: this.$url.url0,
              params: {
                method: 'GetWrite',
                user_id: this.userMsg.ID,
                unit_id: this.unitId
              }
            }).then(res => {
              this.loading = false;
              let data = res.data;
              console.log(data);
              if (data[0]) {
                this.thisWord = data[0];
                // 
                this.theWordState = 1;
                // 
                if (this.thisWord.dictation_percent > 1) {
                  this.theWordNewOrOld = 1;
                } else {
                  this.theWordNewOrOld = 0;
                }
              } else if (data.msg === '') {
                console.log('');
                this.thisTitle = ' - ';
                this.isTest = 0;// 
                this.fnGetDictationTest(0);// 0
                this.$alert(' : )', '', {
                  confirmButtonText: '',
                  callback: () => {
                    return false;
                  }
                })
              } else if (data.msg === '') {
                console.log('');
                this.thisTitle = ' - ';
                this.isTest = 2;// 
                this.countTest = 2;// 
                this.fnGetDictationTest(1);// 1
                this.$alert(' : )', '', {
                  confirmButtonText: '',
                  callback: () => {
                    return false;
                  }
                })
              } else if (data.msg === '') {
                console.log('');
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                  }
                })
              } else if (data.status == 0) {
                console.log('');
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                  }
                })
              }
            })
          },
          // 
          fnGetDictationTest(beforeTest_) {
            console.log('' + beforeTest_);
            this.$ajax({
              method: 'GET',
              url: this.$url.url0,
              params: {
                method: 'GetWriteTest',
                user_id: this.userMsg.ID,
                unit_id: this.unitId,
                before: beforeTest_ // 0 1
              }
            }).then(res => {
              let data = res.data;
              if (data[0]) {
                this.wordList = data;
                this.thisWord = data[0];
              } else {
                this.$alert('!', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                  }
                })
              }
            })
          },
          // 
          fnVerify() {
            // 
            if (this.isTest !== 1) {
              // input0
              if (this.inputStr.length < 1) {
                this.$message({
                  message: '',
                  type: 'warning'
                });
                return false;
              }
              let thisWordName = this.thisWord.word_name.replace(/\/g, '');
              let thisStatus = 0; // 
              if (this.inputStr === thisWordName) {
                thisStatus = 1;// 
              } else {
                thisStatus = 0;// 
              }
              let thisWordObj = {
                id: this.thisWord.id,
                this_name: this.thisWord.word_name.replace(/\/g, ''),
                this_mean: this.thisWord.word_mean,
                this_url: this.thisWord.word_url,
                myVal: this.inputStr,
                status: thisStatus
              };
              this.testArr.push(thisWordObj);
              // 
              this.fnOpenNextTestWord();
            } else { // 
              if (this.againEnter === 2) {
                this.againEnter--;
                let wordName = this.thisWord.word_name.replace(/\/g, '');
                if (wordName === this.inputStr) { // 
                  this.thisWordState = 1;
                  this.isdisabled = true;
                  this.showWord = true;
                  this.againEnter--;
                  this.fnAudioPalyer(this.thisWord.word_url);
                } else { // 
                  this.thisWordState = 2;
                  this.isdisabled = true;
                  this.showWord = true;
                  // 
                  this.theWordState++;
                }
              } else if (this.againEnter === 1) {
                this.againEnter = 2;
                this.thisWordState = 0;
                this.isdisabled = false;
                this.isfocus = true;
                this.showWord = false;
                this.inputStr = '';
              } else if (this.againEnter === 0) {
                this.inputStr = '';
                this.thisWordState = 0;
                this.isdisabled = false;
                this.isfocus = true;
                this.againEnter = 2;
                this.showWord = false;
                this.fnUpdataWordState();
              }
            }
          },
          // 
          fnUpdataWordState() {
            this.$ajax({
              method: 'GET',
              url: this.$url.url0,
              params: {
                method: 'UpdateWriteState',
                id: this.thisWord.id,
                userid: this.userMsg.ID,
                word_state: this.theWordState
              }
            }).then(res => {
              let data = res.data;
              if (data.msg === '') {
                this.fnAllWordState();
                this.fnGetWord();
              }
            })
          },
          // 
          fnOpenNextTestWord() {
            this.thisWordIndex++;
            if (this.thisWordIndex < this.wordList.length) {
              this.inputStr = '';
              this.thisWord = this.wordList[this.thisWordIndex];
            } else {
              // 
              this.$alert('', '', {
                confirmButtonText: '',
                callback: () => {
                  this.fnCountTestScore();
                }
              });
            }
          },
          // 
          fnCountTestScore() {
            let num = 0;
            this.testArr.forEach(item => {
              if (item.status === 1) {
                num++;
              }
            });
            this.score = Math.round((num / this.testArr.length) * 100);
            this.fnSubmitScore();
            sessionStorage.testArr = JSON.stringify(this.testArr);
          },
          // 
          fnSubmitScore() {
            let textbook_id = sessionStorage.textbook_id;
            let study_type = sessionStorage.type_id;
            let type_ = this.isTest == 0 ? 0 : 1;

            // console.log('user_id:' + userMsg.ID);
            // console.log('textbook_id:' + textbook_id);
            // console.log('test_type:' + test_type);
            // console.log('test_score:' + this.score);
            // console.log('test_number:' + this.dataLength);
            // console.log('study_type:' + study_type);
            // console.log('type:' + this.testType);
            // console.log('unit_id:' + this.unitId);
            // console.log('count:' + this.countTestType);
            this.$ajax({
              method: 'GET',
              url: this.$url.url0,
              params: {
                method: 'SaveTestRecord',
                user_id: this.userMsg.ID,
                textbook_id: textbook_id,
                test_type: this.thisVersionName,
                test_score: this.score,
                test_number: this.testArr.length,
                study_type: study_type,
                type: type_, // 0 1
                unit_id: this.unitId,
                count: this.countTest
              }
            }).then(res => {
              let data = res.data;
              if (data.msg === '') {
                this.$router.push({
                  name: 'scorePage',
                  query: {
                    score: this.score,
                    testType: this.isTest
                  }
                });
              } else {
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    return
                  }
                });
              }
            })
          },
          // 
          fnAllWordState() {
            if (this.theWordNewOrOld === 0 && this.theWordState === 1) { // 
              this.oldWordNum++;
            } else if (this.theWordNewOrOld === 0 && this.theWordState > 1) { // 
              this.newWordNum++;
            } else if (this.theWordNewOrOld === 1) { // 
              this.reviewWordNum++;
            }
          },
          // 
          fnSaveStudy() {
            this.$ajax({
              method: 'GET',
              url: this.$url.url1,
              params: {
                method: 'Study',
                user_id: this.userMsg.ID,
                unit_id: this.unitId
              }
            }).then(res => {
              let data = res.data;
              console.log('' + data);
            })
          }
        },
      computed: {
        // 
        thisVersionName() {
          let versionBoxName = sessionStorage.version_name;
          let textbookName = sessionStorage.textbook_name;
          let unitBoxName = sessionStorage.unit_name;
          let leftTitle = versionBoxName + ' - ' + textbookName + ' - ' + unitBoxName;
          return leftTitle;
        }
      },
      mounted() {
        this.fnStudyTime();
        this.fnGetWord();
      },
      created() {
        this.userMsg = JSON.parse(sessionStorage.userMsg);
        this.unitId = sessionStorage.unit_id;
        this.fnSaveStudy();
        // ctrl
        document.onkeydown = (event) => {
          if (event.keyCode === 13) {
            this.fnVerify();
          }
        }
      },
      directives: {
        focus: {
          update: function (el, {value}) {
            if (value) {
              el.focus()
            }
          }
        }
      }
    }
</script>

<style scoped>
  #wordWrite{
    width:100%;
    height:100%;
    background:url(../../static/img/study/space-bg.jpg) no-repeat 0 0;
    position: relative;
  }
  .topInfo{
    width:100%;
    line-height: 40px;
    color:#333;
    font-size:14px;
    text-align: center;
  }
  .centerBox{
    width: 870px;
    height:540px;
    background: url(../../static/img/study/space-bg-small.png) no-repeat 0 0;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
  }
  .nameBox{
    width:94%;
    height:50px;
    line-height: 50px;
    position: relative;
    border-bottom:1px solid #ccc;
    margin:12px auto 0;
  }
  .nameBox .deviceName{
    font-size:16px;
    text-align: center;
    color:#666;
  }
  .version{
    color:#aaa;
    font-size:12px;
    position: absolute;
    top:0;left:2px;
  }
  .rightBtn{
    position: absolute;
    height:50px;
    top:0;right:2px;
  }
  .rightBtn>span{
    float:left;
    width:32px;
    height:100%;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
  }
  .rightBtn>span.help{
    background-image:url(../../static/img/study/help-light.png);
  }
  .rightBtn>span.skin{
    background-image:url(../../static/img/study/skin-light.png);
  }
  .rightBtn>span.close{
    background-image:url(../../static/img/study/close-light.png);
  }
  .rightBtn>span.help:hover{
    background-image:url(../../static/img/study/help-light-hover.png);
  }
  .rightBtn>span.skin:hover{
    background-image:url(../../static/img/study/skin-light-hover.png);
  }
  .rightBtn>span.close:hover{
    background-image:url(../../static/img/study/close-light-hover.png);
  }

  /**/
  .mainCon{
    margin: 80px 120px 0 120px;
    height:380px;
  }
  .mainCon .wordBox{
    width: 100%;
    height:60px;
    line-height:60px;
    font-family: "Arial";
    padding: 0 10px;
    box-sizing:border-box;
  }
  .mainCon .wordBox .word{
    font-size:32px;
    color:rgb(153, 153, 153);
  }
  .mainCon .wordBox .soundmark{
    font-size:16px;
    color:#959694;
  }
  .mainCon .wordBox .rightWord{
    color:#333;
    font-size:32px;
  }
  .mainCon .inputBox{
    position: relative;
    width:100%;
    height:60px;
  }
  .mainCon .inputBox input{
    border:none;
    outline: none;
    -webkit-appearance: none; -moz-appearance: none; -o-appearance: none; appearance: none;
    background-color: #c8c5b4;
    width:100%;
    height:100%;
    padding: 0 10px;
    box-sizing:border-box;
    font-size:32px;
    color:#333;
  }
  .mainCon .inputBox .laba{
    position: absolute;
    top:0;
    left:-50px;
    width:60px;
    height:60px;
    line-height: 60px;
    color:#999;
    font-size:18px;
  }
  .mainCon .inputBox .isRight{
    position: absolute;
    top:0;
    right: -60px;
    width:60px;
    height:60px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size:40px 40px;
  }
  .mainCon .inputBox.right .isRight{
    background-image:url(../../static/img/study/spell_right.png);
  }
  .mainCon .inputBox.wrong .isRight{
    background-image:url(../../static/img/study/spell_wrong.png);
  }
  .mainCon .inputBox.wrong  input{
    color:red;
  }
  .mainCon .mean{
    line-height: 40px;
    font-size: 24px;
    color:#666;
    overflow: hidden;
    margin:16px 0;
  }
  /**/
  .totalBar{
    width:100%;
    height:6px;
    background-color: #ccc;
    margin:8px 0;
    position: relative;
  }
  .totalBar .curBar{
    position: absolute;
    top:0;left:0;
    width: 10%;
    height:6px;
    background: #0a724a;
  }
  /**/
  .botInfo{
    width:90%;
    margin:0 auto;
    color:#666;
    font-size:14px;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
  }
</style>



