<template>
  <div class="courseCon" id="courseCon" v-show="showCourse">
    <el-popover
      ref="popover1"
      placement="top-start"
      width="200"
      trigger="hover"
      content="403000
      ">
    </el-popover>
    <el-popover
      ref="popover2"
      placement="top-start"
      width="200"
      trigger="hover"
      content="
      ">
    </el-popover>
    <el-popover
      ref="popover3"
      placement="top-start"
      width="200"
      trigger="hover"
      content="
      ">
    </el-popover>
    <el-popover
      ref="popover4"
      placement="top-start"
      width="200"
      trigger="hover"
      content="
      ">
    </el-popover>
    <el-popover
      ref="popover5"
      placement="top-start"
      width="200"
      trigger="hover"
      content="
      ">
    </el-popover>
    <el-popover
      ref="popover6"
      placement="top-start"
      width="200"
      trigger="hover"
      content="
      ">
    </el-popover>
    <el-popover
      ref="popover7"
      placement="top-start"
      width="200"
      trigger="hover"
      content="">
    </el-popover>
    <el-popover
      ref="popover8"
      placement="top-start"
      width="200"
      trigger="hover"
      content="25">
    </el-popover>
    <el-popover
      ref="popover9"
      placement="top-start"
      width="200"
      trigger="hover"
      content="">
    </el-popover>

    <ul>
      <li class="words">
        <ul>
          <li class="leftName"></li>
          <li class="item" v-popover:popover1
            @click="fnOpenStudyType(1)">
            <span class="leftIcon" style="background-image:url(../../static/img/memory.png);"></span>
            <span class="name"></span>
          </li>
          <li class="item" v-popover:popover2
              @click="fnOpenStudyType(2)">
            <span class="leftIcon" style="background-image:url(../../static/img/dictation-fff.png);"></span>
            <span class="name"></span>
          </li>
          <li class="item" v-popover:popover3
              @click="fnOpenStudyType(3)">
            <span class="leftIcon" style="background-image:url(../../static/img/write-fff.png);"></span>
            <span class="name"></span>
          </li>
        </ul>
      </li>
      <li class="sentence">
        <ul>
          <li class="leftName"></li>
          <li class="item" v-popover:popover4
              @click="fnOpenStudyType(4)">
            <span class="leftIcon" style="background-image:url(../../static/img/hearing.png);"></span>
            <span class="name"></span>
          </li>
          <li class="item" v-popover:popover5
              @click="fnOpenStudyType(5)">
            <span class="leftIcon" style="background-image:url(../../static/img/translation.png);"></span>
            <span class="name"></span>
          </li>
          <li class="item" v-popover:popover6
              @click="fnOpenStudyType(6)">
            <span class="leftIcon" style="background-image:url(../../static/img/example-write.png);"></span>
            <span class="name"></span>
          </li>
        </ul>
      </li>
      <li class="expand">
        <ul>
          <li class="leftName"></li>
          <li class="item" v-popover:popover7
              @click="fnOpenStudyType(7)">
            <span class="leftIcon" style="background-image:url(../../static/img/voice.png);"></span>
            <span class="name"></span>
          </li>
          <li class="item" v-popover:popover8
              @click="fnOpenStudyType(8)">
            <span class="leftIcon" style="background-image:url(../../static/img/reading.png);"></span>
            <span class="name"></span>
          </li>
          <li class="item" v-popover:popover9
              @click="fnOpenStudyType(9)">
            <span class="leftIcon" style="background-image:url(../../static/img/spoken.png);"></span>
            <span class="name"></span>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'home-course-center',
    props: {
      showCourse: {
        type: Boolean
      }
    },
    components: {},
    data() {
      return {
        contentArr: [
          '403000\n' +
          '      ',
          '\n' +
          '      ',
          '\n' +
          '      ',
          '\n' +
          '      ',
          '\n' +
          '      ',
          '\n' +
          '      ',
          '',
          '25',
          ''
        ],
        moduleList: [
          {
            name: ''
          },
          {
            name: ''
          },
          {
            name: ''
          }
        ]
      }
    },
    methods: {
      fnOpenStudyType(type_) {
        if (type_ <= 6) {
          this.$store.commit('updateShowGoStudyCenter');
          this.$router.replace('/home');
          this.$bus.emit('openStudyType', type_);
        } else if (type_ === 7) { // 
          this.$router.replace('/home/homeExtendVoice');
          this.$store.commit('updateShowGoStudyCenter', true);
        }else if (type_ === 8) { // 
          this.$router.replace('/home/readpage');
          // this.$store.commit('updateShowGoStudyCenter', true);
        } else if (type_ === 9) { // 
          this.$router.replace('/home/homeExtendTongue');
          this.$store.commit('updateShowGoStudyCenter', true);
        } else {
          this.$alert(': )', '', {
            confirmButtonText: '',
            callback: () => {
              return
            }
          });
        }
      },
      // vuex
      fnUpdateType(type_) {
        sessionStorage.type_id = type_;
        this.$store.commit('updateTypeId', type_);
        this.$store.commit('updateDeviceBoxTitle', this.moduleList[type_].name);
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>
  .courseCon {
    width: 1000px;
    height: 620px;
    padding: 100px 80px;
    box-sizing: border-box;
    border: 1px solid #666;
    border-top: none;
    background: #fff;
    position: absolute;
    top: 80px;
    left: -198px;
    z-index: 2;
  }

  .courseCon > ul {
    width: 100%;
  }

  .courseCon > ul > li {
    width: 100%;
    height: 120px;
    border-bottom: 1px dashed #999;
  }

  .courseCon > ul > li > ul {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
  }

  .courseCon > ul > li > ul > li {
    width: 220px;
    height: 80px;
    line-height: 80px;
    background-color: #008c72;
    text-align: center;
    color: #fff;
    margin: 20px 0;
    font-size: 16px;
  }

  .courseCon > ul > li.sentence > ul > li {
    background-color: #31a594;

  }

  .courseCon > ul > li.expand > ul > li {
    background-color: #d6f1f1;
    color: #666;
  }

  .courseCon > ul  li span.leftIcon {
    display: inline-block;
    vertical-align: top;
    width: 44px;
    height: 80px;
    background:url(../../static/img/memory.png) no-repeat center;
  }

  .courseCon > ul > li.words  li.item:hover{
    background-color: #02987c;
  }
  .courseCon > ul > li.sentence  li.item:hover{
    background-color: #299283;
  }
  .courseCon > ul > li.expand  li.item:hover{
    background-color: #c9e4e4;
  }

  .courseCon > ul  li span.name {
    display: inline-block;
    vertical-align: top;
  }

  .courseCon > ul > li > ul > li.leftName {
    width: 120px;
    height: 80px;
    background: #fff;
    color: #999;
    font-size: 16px;
    line-height: 80px;
    padding: 0;
    margin-left: 0;
  }
</style>



