<template>
  <div class="wordWrapper" v-loading="loading">
    <!---->
    <div class="version">
      <div class="versionName">{{thisVersionName}}</div>
      <div class="tipBox">
        <span class="tipBtn" @click="openTip()"></span>
        <!---->
        <div class="tipCon" id="tipCon" v-show="showTip">
          <h4></h4>
          <div class="context clearfix">
            <span class="leftImg"></span>
            <span class="txt">
				</span>
          </div>
          <div class="closeBtn" @click="closeTip()"></div>
        </div>
      </div>
    </div>
    <!---->
    <div class="timeBox clearfix">
      <span class="curTime">{{time}}</span>
      <span class="wordNum" v-show="false"><i class="red">10</i><i class="red">10</i></span>
      <span class="reviewBtn" @click="fnGoStudy()"></span>
    </div>
    <!---->
    <div class="wordList">
      <span :class="{
        bold: item.countdown%60==0,
        small: item.countdown%60!=0
        }" v-for="item in wordList"
        @mouseenter="fnshowCon(item, $event)"
        @mouseleave="fnCloseCon()">
        {{item.word_name?item.word_name:item.sentence}}
      </span>
      <!--<span class="bold">advise</span>-->
      <!--<span class="small">adjective</span>-->
    </div>

    <div class="box" v-show="showCon" :style="{left: leftP+'px', top: topP+'px'}">
      <div class="translate" id="translate">
        <i>{{boxTranslate}}</i>
      </div>
      <div class="thisStudyNum" id="thisStudyNum">
        {{studyNum}}  {{errNum}}
      </div>
      <div class="memoryNum" id="memoryNum">
        {{intensityMemory}}%
      </div>
      <div class="studyTimeOver" id="studyTimeOver">
        {{memoryTime}}
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'home-memory-tracer',
    components: {},
    data() {
      return {
        showTip: false, // 
        loading: false,
        userMsg: {},
        typeId: 0,
        textbookTd: 0,
        time: '', // 
        allWord: 0, // 
        presentWord: 0, // 
        wordList: [{
          "id":2,
          "user_id":1,
          "unit_id":1,
          "word_name":"adjective",
          "word_mean":"n.",
          "phonogram":"['iktiv]",
          "word_url":"\UploadeFile\Video\adjective.wav",
          "sentence":"What is the adjective?",
          "sentence_mean":"",
          "neworold_word":2,
          "percent":12,
          "countdown":0,
          "downtime":"2018-03-13T19:32:53",
          "study_times":1,
          "correct_times":0,
          "wrong_number":1
        }],
        showCon: false, // 
        leftP: 0, // left
        topP: 0, // top
        boxTranslate: '', // 
        studyNum: 0, // 
        errNum: 0, // 
        intensityMemory: 0, // 
        memoryTime: '00:00:00' // 
      }
    },
    methods: {
      // 
      openTip() {
        this.showTip = this.showTip ? false : true;
      },
      // 
      closeTip() {
        this.showTip = false;
      },
      // 
      fnGetAllWord() {
        this.loading = true;
        this.$ajax({
          method: 'GET',
          url: this.$url.url1,
          params: {
            method: 'Memorytracking',
            user_id: this.userMsg.ID,
            textbookid: this.textbookTd,
            type: this.typeId
          }
        }).then(res => {
          this.loading = false;
          let data = res.data;
          this.wordList = data;
        })
      },
      // 
      updateTime() {
        let date = new Date();
        let year = date.getFullYear(); // 
        let month = date.getMonth() + 1; // 
        let day = date.getDate(); // 
        let h = date.getHours(); // 
        let m = date.getMinutes(); // 
        let s = date.getSeconds(); // 
        this.time = `${year} ${month} ${day} ${h}: ${m}: ${s}`;
      },
      // 
      fnshowCon(item_, event) {
        this.showCon = true;
        this.leftP = event.clientX + 10;
        this.topP = event.clientY + 20;
        this.boxTranslate = item_.word_mean?item_.word_mean:item_.sentence_mean;
        this.studyNum = item_.study_times;
        this.errNum = item_.wrong_number;
        this.intensityMemory = item_.percent;
        this.memoryTime = this.thisMemoryTime(item_.countdown);
      },
      fnCloseCon() {
        this.showCon = false;
      },
      // 
      thisMemoryTime(time_) {
        let hour_ = parseInt(time_ / 3600);
        let minute_ = parseInt((time_ % 3600) / 60);
        let second_ = time_ % 60;
        if (second_ == 0) {
          return ``;
        } else {
          return `${hour_} : ${minute_} : ${second_}`;
        }
      },
      // 
      fnGoStudy() {
        switch (parseInt(this.typeId)){
          case 1:
            this.$router.push({
              name: 'centerWordStudy'
            });
            break;
          case 2:
            this.$router.push({
              name: 'centerWordListen'
            });
            break;
          case 3:
            this.$router.push({
              name: 'centerWordWrite'
            });
            break;
          case 4:
            this.$router.push({
              name: 'centerSentenceListen'
            });
            break;
          case 5:
            this.$router.push({
              name: 'centerSentenceTranslate'
            });
            break;
          case 6:
            this.$router.push({
              name: 'centerSentenceWrite'
            });
            break;
        }
      }
    },
    computed: {
      // 
      thisVersionName() {
        let versionBoxName = sessionStorage.version_name;
        let textbookName = sessionStorage.textbook_name;
        let unitBoxName = sessionStorage.unit_name;
        let leftTitle = versionBoxName + ' - ' + textbookName + ' - ' + unitBoxName;
        return leftTitle;
      },
    },
    mounted() {
      this.updateTime();
      this.fnGetAllWord();
    },
    created() {
      this.userMsg = JSON.parse(sessionStorage.userMsg);
      this.textbookTd = sessionStorage.textbook_id;
      this.typeId = sessionStorage.type_id;
    }
  }
</script>

<style scoped>
  .wordWrapper {
    width: 1000px;
    min-height: 640px;
    background-color: #fff;
    margin: 20px auto;
    padding: 10px;
    box-sizing: border-box;
    /*position: relative;*/
  }
  .box{
    padding: 10px 15px;
    width: 240px;
    font-size: 14px;
    color: #fff;
    background-color: #008c72;
    position: absolute;
    left: 0;
    top: 0;
  }
  .box div{
    line-height: 24px;
    border-bottom: 1px solid #fff;
  }

  /**/
  .version {
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    color: #fff;
    background-color: #008c72;
    padding: 0 12px;
    box-sizing: border-box;
  }

  .version > .versionName {
    float: left;
  }

  .version > .tipBox {
    float: right;
    position: relative;
  }

  .version > .tipBox .tipBtn {
    cursor: pointer;
  }

  .version > .tipBox .tipBtn:hover {
    color: darkorange;
  }

  /**/
  .timeBox {
    margin-top: 10px;
    width: 100%;
    height: 26px;
    line-height: 26px;
    padding: 6px 0;
    border-bottom: 1px solid #ddd;
    font-size: 14px;
    color: #666;
  }

  .timeBox .curTime {
    float: left;
  }

  .timeBox .wordNum {
    float: left;
    margin-left: 30px;
  }

  .timeBox .wordNum .red {
    color: red;
  }

  .timeBox .reviewBtn {
    float: right;
    padding: 0 10px;
    color: #fff;
    background-color: #ff9900;
  }

  .timeBox .reviewBtn:hover {
    cursor: pointer;
    background-color: orange;
  }

  /**/
  .wordList {
    padding: 20px;
    box-sizing: border-box;
  }

  .wordList > span {
    margin-right: 8px;
    cursor: pointer;
  }

  .wordList .bold {
    font-size: 24px;
    font-weight: 600;
  }

  .wordList .small {
    font-size: 13px;
    color: #999;
    vertical-align: text-top;
  }

  .cover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    z-index: 20;
  }

  /**/
  .tipCon {
    position: absolute;
    top: 38px;
    right: -12px;
    width: 660px;
    height: 300px;
    background-color: #fff;
    z-index: 21;
    padding: 10px;
    box-sizing: border-box;
    color: #666;
  }

  .tipCon h4 {
    height: 30px;
    line-height: 30px;
    color: #fff;
    padding: 0 10px;
    background-color: #008c72;
    box-sizing: border-box;
  }

  .tipCon .context {
    margin-top: 10px;
  }

  .tipCon .context .leftImg {
    float: left;
    width: 240px;
    height: 200px;
    background: url(../../static/img/study/test-result-klm.png) no-repeat center center;
  }

  .tipCon .context .txt {
    float: left;
    width: 400px;
    font-size: 15px;
    color: #333;
    line-height: 26px;
    padding-top: 26px;
  }

  .tipCon .closeBtn {
    width: 100px;
    height: 36px;
    background-color: #ce0324;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    line-height: 36px;
    margin-left: 530px;
  }

  .tipCon .closeBtn:hover {
    opacity: .8;
  }
</style>



