<template>
  <div id="studyTest">
    <testHead :title="test_type"></testHead>
    <div class="content" v-loading="loading">
      <div class="itemBox">
        <h4></h4>
        <ul class="itemList">
          <li :class="{selItem:false}" v-for="(item, index) in e_cList">
            <div class="wordLine">
              <span class="num">{{index + 1}}.</span>
              <span class="word">{{item.word_name.replace(/\/g,'')}}</span>
            </div>
            <div class="selectorBox">
              <label v-for="vo in item.chinese">
                <input type="radio" :value="vo"
                       v-model="item.checked"
                       :name="item.id"
                       :data-type="vo.type"/>
                {{vo.content}}
              </label>
            </div>
          </li>
          <!--<li>
            <div class="wordLine">
              <span class="num">2.</span>
              <span class="word">alive</span>
            </div>
            <div class="selectorBox">
              <label for="y2-a"><input type="radio" name="Y-itemsel2" id="y2-a"/>n. </label>
              <label for="y2-b"><input type="radio" name="Y-itemsel2" id="y2-b"/>adv. prep. ......</label>
              <label for="y2-c" ><input type="radio" name="Y-itemsel2" id="y2-c"/>adv. prep. ......adv. prep. ......</label>
              <label for="y2-d" ><input type="radio" name="Y-itemsel2" id="y2-d"/>adj. </label>
            </div>
          </li>-->
        </ul>
      </div>
      <div class="itemBox">
        <h4></h4>
        <ul class="itemList">
          <li v-for="(item, index) in c_eList">
            <div class="wordLine">
              <span class="num">{{index + 1}}.</span>
              <span class="word">{{item.word_mean}}</span>
            </div>
            <div class="selectorBox">
              <label v-for="vo in item.english">
                <input type="radio" :value="vo"
                       v-model="item.checked"
                       :name="item.id"
                       :data-type="vo.type"/>
                {{vo.content.replace(/\/g,'')}}
              </label>
            </div>
          </li>
          <!--<li>
            <div class="wordLine">
              <span class="num">1.</span>
              <span class="word">adj. </span>
            </div>
            <div class="selectorBox">
              <label for="h1-a"><input type="radio" name="H-itemsel1" id="h1-a"/>atract</label>
              <label for="h1-b"><input type="radio" name="H-itemsel1" id="h1-b"/>attend</label>
              <label for="h1-c"><input type="radio" name="H-itemsel1" id="h1-c"/>arrive</label>
              <label for="h1-d"><input type="radio" name="H-itemsel1" id="h1-d"/>active</label>
            </div>
          </li>-->
        </ul>
      </div>
      <div class="itemBox">
        <h4></h4>
        <ul class="itemList">
          <li v-for="(item, index) in listenList">
            <div class="wordLine">
              <span class="num">{{index + 1}}.</span>
              <span class="readBtn" @click="fnVoicePlaying(item)"></span>
            </div>
            <div class="selectorBox">
              <label v-for="vo in item.chinese">
                <input type="radio" :value="vo"
                       v-model="item.checked"
                       :name="item.id"
                       :data-type="vo.type"/>
                {{vo.content}}
              </label>
            </div>
          </li>
          <!--<li>
            <div class="wordLine">
              <span class="num">1.</span>
              <span class="readBtn" ></span>
            </div>
            <div class="selectorBox">
              <label for="y2-a"><input type="radio" name="Y-itemsel2" id="y2-a"/>n. </label>
              <label for="y2-b"><input type="radio" name="Y-itemsel2" id="y2-b"/>adv. prep. ......</label>
              <label for="y2-c"><input type="radio" name="Y-itemsel2" id="y2-c"/>adv. prep. ......adv. prep. ......</label>
              <label for="y2-d"><input type="radio" name="Y-itemsel2" id="y2-d"/>adj. </label>
            </div>
          </li>-->
        </ul>
      </div>
    </div>
    <home-foot></home-foot>
    <audio-tag></audio-tag>
  </div>
</template>

<script>
  import testHead from '../../components/testHead'
  import homeFoot from '../../components/homeFoot'
  import audioTag from '../../components/audioTag'

  export default {
    name: 'word-study-test',
    components: {testHead, homeFoot, audioTag},
    data() {
      return {
        loading: false,
        userMsg: {},
        typeId: 0,
        textbookTd: 0,
        dataArr: [],
        dataLength: 0,
        itemsLength: 0,
        e_cList: [],
        c_eList: [],
        listenList: [],
        score: 0,
        test_type: '',
        testObjArr: [],
        tit: ' - '
      }
    },
    methods: {
      // 
      fnGetTestList() {
        console.log('');
        this.loading = true;
        this.$ajax({
          method: 'GET',
          url: this.$url.url1,
          params: {
            method: 'TestReview',
            user_id: this.userMsg.ID,
            textbookid: this.textbookTd,
            type: this.typeId
          }
        }).then(res => {
          this.loading = false;
          let data = res.data;
          this.fnArrProcessor(data);

          if (data && JSON.stringify(data) === '[]') {
            this.$alert('', '', {
              confirmButtonTex: '',

              callback: () => {
                this.$router.push('/home');
              }
            });
          } else {
            this.dataArr = data;
            this.dataLength = data.length;
            this.itemsLength = parseInt(this.dataLength / 3);

            // 
            this.$bus.emit('fnsetTimeOut', data.length);

            this.fnPushList();
          }
        })
      },
      // 
      fnArrProcessor(arr_) {
        arr_.forEach((item, index) => {
          item.checked = {};
        })
      },
      // pushpush
      fnPushList() {
        this.fnPushItemList(this.e_cList);
        this.fnPushItemList(this.c_eList);
        this.listenList = this.dataArr;
        sessionStorage.e_cList = JSON.stringify(this.e_cList);
        sessionStorage.c_eList = JSON.stringify(this.c_eList);
        sessionStorage.listenList = JSON.stringify(this.listenList);
        // console.log(''+this.e_cList.length +'-'+ this.c_eList.length +'-'+ this.listenList.length);
      },
      fnPushItemList(arr_) {
        for (let i = 0; i < this.itemsLength; i++) {
          this.dataLength--;
          let random = parseInt(Math.random() * this.dataLength + 1);
          arr_.push(this.dataArr.splice(random, 1)[0]);
        }
      },
      // 
      fnVoicePlaying(item_) {
        this.$bus.emit('audioPlayer', item_.word_url);
      },
      // 
      fnNextArrProcessor() {
        // inputindex
        // let testObjArr = [];
        let testArr = document.querySelectorAll('.itemList>li');
        testArr.forEach((item, index) => {
          let newItemObj = {};
          let myCheckedIndex = -1;
          let answerIndex = -1;
          let isCorrect = false;
          let itemInput = item.querySelectorAll('input');
          itemInput.forEach((item_, index_) => {
            if (item_.dataset.type === '1') {
              answerIndex = index_;
            }
            if (item_.checked) {
              myCheckedIndex = index_;
            }
            if (myCheckedIndex >= 0 && myCheckedIndex === answerIndex) {
              isCorrect = true;
            }
          });
          newItemObj.myCheckedIndex = myCheckedIndex;
          newItemObj.answerIndex = answerIndex;
          newItemObj.isCorrect = isCorrect;
          newItemObj.liIndex = index;
          this.testObjArr.push(newItemObj);
        });
        // console.log(JSON.stringify(testObjArr));
        let correctNum = 0;
        this.testObjArr.forEach((item, index) => {
          if (item.isCorrect) {
            correctNum++
          }
        });
        this.score = Math.round((correctNum / this.testObjArr.length) * 100);
        sessionStorage.testList = JSON.stringify(this.testObjArr);
        console.log('' + this.score + '- ' + correctNum);
        this.fnSubmitScore();
      },
      // 
      fnSubmitScore() {
        this.$ajax({
          method: 'GET',
          url: this.$url.url0,
          params: {
            method: 'SaveTestRecord',
            user_id: this.userMsg.ID,
            textbook_id: this.textbookTd,
            test_type: this.tit + ' - ',
            test_score: this.score,
            test_number: this.dataLength,
            study_type: this.typeId,
            type: 3, // 0 1
            unit_id: 1,
            count: this.countTestType
          }
        }).then(res => {
          let data = res.data;
          if (data.msg == '') {
            this.$router.push({
              name: 'wordStudyScore',
              query: {
                score: this.score,
                testType: this.testType,
                countTestType: this.countTestType
              }
            });
          } else {
            this.$alert('', '', {
              confirmButtonText: '',
              callback: () => {
                return
              }
            });
          }
        })
      }
    },
    computed: {
      // 0   1
      testType() {
        let testType_ = parseInt(this.$route.query.testType) === 0 ? 0 : 1;
        return testType_;
      },
      // 0   1
      countTestType() {
        let countTestType_ = this.$route.query.countTestType ? parseInt(this.$route.query.countTestType) : 1;
        return countTestType_;
      },
      // 
      scoreType() {
        let scoreType_ = this.testType === 0 ? '' : '';
        return scoreType_;
      },
    },
    mounted() {
      this.fnGetTestList();
      this.test_type = ' -  - ';
    },
    created() {
      this.userMsg = JSON.parse(sessionStorage.userMsg);
      this.textbookTd = sessionStorage.textbook_id;
      this.typeId = sessionStorage.type_id;
      // 
      this.$bus.on('submitGrade', () => {
        // this.$alert('', '', {
        //   confirmButtonTex: '',
        //   callback: () => {
        //     this.fnNextArrProcessor();
        //   }
        // });
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success'
        }).then(() => { // 
          this.fnNextArrProcessor();
        }).catch(() => { // 
          return
        });
      });
    },
    beforeDestroy() {
      this.$bus.off('submitGrade');
    }
  }
</script>

<style scoped>
  .content {
    width: 1000px;
    min-height: 800px;
    margin: 70px auto 20px;
    background-color: #fff;
    padding: 10px;
    box-sizing: border-box;
  }

  .content > .itemBox {
    margin-top: 10px;
    width: 100%;
  }

  .content > .itemBox > h4 {
    height: 36px;
    line-height: 36px;
    color: #fff;
    background-color: #008c72;
    font-size: 16px;
    padding: 0 10px;
    box-sizing: border-box;
  }

  .content > .itemBox > ul {
    width: 100%;
    border: 1px solid #ccc;
    border-top: none;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    box-sizing: border-box;
  }

  .content > .itemBox > ul > li {
    width: 50%;
    padding: 20px;
    background-color: #fdfbf4;
    border-right: 1px dashed #959595;
    border-top: 1px dashed #959595;
    box-sizing: border-box;
  }

  .content > .itemBox > ul > li.selItem {
    background-color: #eee9e3;
  }

  .content > .itemBox > ul > li:nth-child(2n) {
    border-right: none;
  }

  .content > .itemBox > ul > li:nth-child(1),
  .content > .itemBox > ul > li:nth-child(2) {
    border-top: none;
  }

  .content > .itemBox > ul > li .wordLine {
    color: #333;
    font-weight: 600;
    line-height: 30px;
    font-size: 18px;
    font-family: "SimSun";
  }

  .content > .itemBox > ul > li .wordLine .readBtn {
    display: inline-block;
    vertical-align: top;
    width: 80px;
    height: 30px;
    background: url(../../../static/img/study/hearing-1.png) no-repeat left center;
    cursor: pointer;
  }

  .content > .itemBox > ul > li .wordLine .readBtn:hover {
    opacity: .7;
  }

  .content > .itemBox > ul > li .selectorBox {
    margin-left: 20px;
    margin-top: 10px;
  }

  .content > .itemBox > ul > li .selectorBox label {
    display: block;
    line-height: 28px;
    position: relative;
    padding-left: 26px;
    cursor: pointer;
    color: #666;
    font-size: 16px;
  }

  .content > .itemBox > ul > li .selectorBox label input {
    position: absolute;
    top: 5px;
    left: 0;
  }

  .content > .itemBox > ul > li .selectorBox label:hover {
    background-color: #ccc;
  }
</style>



