<template>
    <div id="sentenceWrite">
      <div class="topInfo">
        <!--<span>00:20:40</span>-->
        <!--<span>00:34:40</span>-->
        <!--<span>13%</span>-->
      </div>
      <!---->
      <div class="centerBox" v-loading="loading">
        <!---->
        <div class="nameBox">
          <div class="deviceName">{{thisTitle}}</div>
          <div class="version">{{thisVersionName}}</div>
          <div class="rightBtn clearfix">
            <!--<span class="help"></span>-->
            <!--<span class="skin"></span>-->
            <span class="close" @click="fnclosePage()"></span>
          </div>
        </div>
        <!---->
        <div class="mainCon">
          <!---->
          <div class="mean">{{thisSentence.sentence_mean}}</div>
          <!---->
          <div class="inputBox ">
            <div class="laba"></div>
            <input type="text" v-model="inputStr" autofocus id="thisInput"
                   v-focus="isfocus"
                   :class="{wrong: thisSentenceState>1}"
                   :disabled="isdisabled"
                   oncopy="return false;"
                   onpaste="return false;"
                   oncut="return false;" />
            <div class="isRight" v-show="thisSentenceState==1"></div>
            <div class="isWrong" v-show="thisSentenceState>1"></div>
          </div>
          <!---->
          <div class="totalBar"
               :style="{visibility: isTest==1?'visible':'hidden'}"
               :title="'' + thisSentence.SenWrite_per">
          <span class="curBar"
                :style="{width: thisSentence.SenWrite_per + '%'}"></span>
          </div>
          <!-- wordBox  wordBox1-->
          <div class="wordBox"
               :class="{wordBox1: thisSentenceState>1}"
               v-show="showSentence">
            <!--v-show="showSentence"-->
            {{thisSentence.sentence}}
          </div>
        </div>
        <!---->
        <div class="botInfo">
          <span> {{studyTime}}</span>
          <span v-if="isTest == 1"> [  {{newWordNum}}{{oldWordNum}}{{reviewWordNum}} ]</span>
          <span v-else>{{thisSentenceIndex+1}}/{{sentenceList.length}}</span>
        </div>
        <audio src="" id="audio" autoplay="autoplay"></audio>
      </div>
    </div>
</template>

<script>
    export default {
        name: 'sentence-write',
        components: {},
        data() {
            return {
              loading: false,
              userMsg: {},
              unitId: 0,
              typeId: 1, // ID
              thisTitle: '',
              studyTime: '00:00:00', // 
              newWordNum: 0, // 
              oldWordNum: 0, // 
              reviewWordNum: 0, // 
              showSentence: false, // 
              showMsg: false, // 
              thisSentence: { // 
                id: 102,
                sentence: 'The giraffe has a long neck.',
                sentence_mean: '',
                sentence_url: '\\UploadeFile\\Video\\The_giraffe_has_a_long_neck.wav'
              },
              sentenceList: [], // 
              thisSentenceIndex: 0, // index
              thisSentenceState: 0, // 0   1   2 
              theSentenceNewOrOld: 0, //  0   0
              theSentenceState: 1, //  1  1
              isTest: 1, //  0:  1:  2:
              countTest: 1, //  1  2
              testArr: [], // 
              score: 0, // 
              againEnter: 2, // 2-1
              inputStr: '', // input
              isfocus: true, // 
              isdisabled: false // input
            }
        },
        methods: {
          fnclosePage() {
            this.$router.go(-1);
          },
          // 
          fnStudyTime() {
            let time = 0;
            setInterval(() => {
              time++;
              let hour = parseInt(time / 3600) < 10 ? '0' + parseInt(time / 3600) : parseInt(time / 3600);
              let minute = parseInt(time % 3600 / 60) < 10 ? '0' + parseInt(time % 3600 / 60) : parseInt(time % 3600 / 60);
              let second = parseInt(time % 60) < 10 ? '0' + parseInt(time % 60) : parseInt(time % 60);
              this.studyTime = hour + ':' + minute + ':' + second;
            }, 1000)
          },
          // 
          fnAudioPalyer(url_) {
            let audio = document.querySelector('#audio');
            audio.src = this.$url.url2 + url_;
          },
          // 
          fnGetSentence() {
            this.loading = true;
            this.$ajax({
              method: 'GET',
              url: this.$url.url1,
              params: {
                method: 'Sentence',
                user_id: this.userMsg.ID,
                unit_id: this.unitId,
                type: this.typeId
              }
            }).then(res => {
              this.loading = false;
              let data = res.data;
              // console.log(data);
              if (data[0]) { // 
                this.thisSentence = data[0];
                // 
                this.theSentenceState = 1;
                // 0
                if (this.thisSentence.SenWrite_per > 0) {
                  this.theSentenceNewOrOld = 1;
                } else {
                  this.theSentenceNewOrOld = 0;
                }
              } else if (data == 4) { // 
                this.$alert(' : )', '', {
                  confirmButtonText: '',
                  callback: () => {
                    console.log('');
                    this.thisTitle = ' - ';
                    this.isTest = 0;
                    this.fnGetTestList();

                    return false;
                  }
                })
              } else if (data == 2) { // 
                this.$alert(' : )', '', {
                  confirmButtonText: '',
                  callback: () => {
                    console.log('');
                    this.thisTitle = ' - ';
                    this.isTest = 2;
                    this.fnGetTestList();

                    return false;
                  }
                })
              } else if (data == 3) { // 
                this.$alert(' : (', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                    return false;
                  }
                })
              } else if (data == 0) { // 
                this.$alert(' : (', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                    return false;
                  }
                })
              }
            })
          },
          // 
          fnGetNextSentence() {
            this.fnAllSentenceState();
            this.loading = true;
            this.$ajax({
              method: 'GET',
              url: this.$url.url1,
              params: {
                method: 'getnextSentence',
                id: this.thisSentence.id,
                neworold_word: this.theSentenceState,
                user_id: this.userMsg.ID,
                unit_id: this.unitId,
                type: this.typeId
              }
            }).then(res => {
              this.loading = false;
              let data = res.data;
              // console.log(data);
              if (data[0]) { // 
                this.thisSentence = data[0];
                // 
                this.theSentenceState = 1;
                // 0
                if (this.thisSentence.SenWrite_per > 0) {
                  this.theSentenceNewOrOld = 1;
                } else {
                  this.theSentenceNewOrOld = 0;
                }
              } else if (data == 4) { // 
                this.$alert(' : )', '', {
                  confirmButtonText: '',
                  callback: () => {
                    console.log('');
                    this.thisTitle = ' - ';
                    this.isTest = 0;
                    this.fnGetTestList();

                    return false;
                  }
                })
              } else if (data == 2) { // 
                this.$alert(' : )', '', {
                  confirmButtonText: '',
                  callback: () => {
                    console.log('');
                    this.thisTitle = ' - ';
                    this.isTest = 2;
                    this.fnGetTestList();

                    return false;
                  }
                })
              } else if (data == 3) { // 
                this.$alert(' : (', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                    return false;
                  }
                })
              } else if (data == 0) { // 
                this.$alert(' : (', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                    return false;
                  }
                })
              }
            })
          },
          // 
          fnGetTestList() {
            this.loading = true;
            this.$ajax({
              method: 'GET',
              url: this.$url.url1,
              params: {
                method: 'getSentence',
                unit_id: this.unitId,
                type: 1
              }
            }).then(res => {
              this.loading = false;
              let data = res.data;
              if (data[0]) {
                this.sentenceList = data;
                this.thisSentence = data[0];
              } else {
                this.$alert('!', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.push('/home');
                  }
                })
              }
            })
          },
          // 
          fnVerify() {
            let myVal = this.fndisposeSen(this.inputStr);
            let rightAnswer = this.fndisposeSen(this.thisSentence.sentence);
            // 
            if (this.isTest !== 1) {
              // 
              // input0
              if (this.inputStr.length < 1) {
                this.$message({
                  message: '',
                  type: 'warning'
                });
                return false;
              }
              // 
              let answerType = 1;// 
              answerType = (myVal === rightAnswer) ? 1 : 0;

              let thisAnswerObj = {
                this_name: this.thisSentence.sentence,
                this_mean: this.thisSentence.sentence_mean,
                status: answerType,
                myVal: myVal
              };
              this.testArr.push(thisAnswerObj);
              // 
              this.fnOpenNextTestSentence();
            } else { // 
              if (this.againEnter === 2) {
                this.againEnter--;
                this.showSentence = true;
                if (myVal === rightAnswer) { // 
                  this.thisSentenceState = 1;
                  this.isdisabled = true;
                  this.againEnter--;
                } else { // 
                  this.thisSentenceState = 2;
                  this.isdisabled = true;
                  // 
                  this.theSentenceState = 2;
                }
              } else if (this.againEnter === 1) {
                this.againEnter = 2;
                this.showSentence = true;
                this.thisSentenceState = 2;
                this.isdisabled = false;
                this.isfocus = true;
                this.inputStr = '';
              } else if (this.againEnter === 0) {
                this.againEnter = 2;
                this.showSentence = false;
                this.isdisabled = false;
                this.isfocus = true;
                this.inputStr = '';
                this.thisSentenceState = 0;
                this.fnGetNextSentence();
              }
            }
          },
          // 
          fnOpenNextTestSentence() {
            this.thisSentenceIndex++;
            if (this.thisSentenceIndex < this.sentenceList.length) {
              this.inputStr = '';
              this.thisSentence = this.sentenceList[this.thisSentenceIndex];
            } else { // 
              this.$alert('',
                ''
              ).then(() => {
                this.fnCountTestScore();
              })
            }
          },
          // 
          fnCountTestScore() {
            let num = 0;
            this.testArr.forEach(item => {
              if (item.status === 1) {
                num++;
              }
            });
            this.score = Math.round((num / this.testArr.length) * 100);
            this.fnSubmitScore();
            sessionStorage.testArr = JSON.stringify(this.testArr);
          },
          // 
          fnSubmitScore() {
            let textbook_id = sessionStorage.textbook_id;
            let study_type = sessionStorage.type_id;
            let type_ = this.isTest == 0 ? 0 : 1;

            // console.log('user_id:' + userMsg.ID);
            // console.log('textbook_id:' + textbook_id);
            // console.log('test_type:' + test_type);
            // console.log('test_score:' + this.score);
            // console.log('test_number:' + this.dataLength);
            // console.log('study_type:' + study_type);
            // console.log('type:' + this.testType);
            // console.log('unit_id:' + this.unitId);
            // console.log('count:' + this.countTestType);
            this.$ajax({
              method: 'GET',
              url: this.$url.url0,
              params: {
                method: 'SaveTestRecord',
                user_id: this.userMsg.ID,
                textbook_id: textbook_id,
                test_type: this.thisVersionName,
                test_score: this.score,
                test_number: this.testArr.length,
                study_type: study_type,
                type: type_, // 0 1
                unit_id: this.unitId,
                count: this.countTest
              }
            }).then(res => {
              let data = res.data;
              if (data.msg === '') {
                console.log(data);
                this.$router.push({
                  name: 'scorePage',
                  query: {
                    score: this.score,
                    testType: this.isTest
                  }
                });
              } else {
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    return
                  }
                });
              }
            })
          },
          // 
          fnAllSentenceState() {
            if (this.theSentenceNewOrOld === 0 && this.theSentenceState === 1) { // 
              this.oldWordNum++;
            } else if (this.theSentenceNewOrOld === 0 && this.theSentenceState > 1) { // 
              this.newWordNum++;
            } else if (this.theSentenceNewOrOld > 0) { // 
              this.reviewWordNum++;
            }
          },
          // 
          fndisposeSen(sentence_) {
            if (typeof sentence_ === 'string') {
              return sentence_.replace('.', '');
            } else {
              return sentence_;
            }
          },
          // 
          fnSaveStudy() {
            this.$ajax({
              method: 'GET',
              url: this.$url.url1,
              params: {
                method: 'Study',
                user_id: this.userMsg.ID,
                unit_id: this.unitId
              }
            }).then(res => {
              let data = res.data;
              console.log('' + data);
            })
          }
        },
      computed: {
        // 
        thisVersionName() {
          let versionBoxName = sessionStorage.version_name;
          let textbookName = sessionStorage.textbook_name;
          let unitBoxName = sessionStorage.unit_name;
          let leftTitle = versionBoxName + ' - ' + textbookName + ' - ' + unitBoxName;
          return leftTitle;
        }
      },
      mounted() {
        this.fnGetSentence();
        this.fnStudyTime();
      },
      created() {
        this.userMsg = JSON.parse(sessionStorage.userMsg);
        this.unitId = sessionStorage.unit_id;
        this.typeId = sessionStorage.type_id;
        this.fnSaveStudy();
        // ctrl
        document.onkeydown = (event) => {
          if (event.keyCode === 13) {
            this.fnVerify();
          }
        }
      },
      directives: {
        focus: {
          update: function (el, {value}) {
            if (value) {
              el.focus()
            }
          }
        }
      }
    }
</script>

<style scoped>
  #sentenceWrite{
    width:100%;
    height:100%;
    background:url(../../static/img/study/space-bg.jpg) no-repeat 0 0;
    position: relative;
  }
  .topInfo{
    width:100%;
    line-height: 40px;
    color:#333;
    font-size:14px;
    text-align: center;
  }
  .centerBox{
    width: 870px;
    height:540px;
    background: url(../../static/img/study/space-bg-small.png) no-repeat 0 0;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
  }
  .nameBox{
    width:94%;
    height:50px;
    line-height: 50px;
    position: relative;
    border-bottom:1px solid #ccc;
    margin:12px auto 0;
  }
  .nameBox .deviceName{
    font-size:16px;
    text-align: center;
    color:#666;
  }
  .version{
    color:#aaa;
    font-size:12px;
    position: absolute;
    top:0;left:2px;
  }
  .rightBtn{
    position: absolute;
    height:50px;
    top:0;right:2px;
  }
  .rightBtn>span{
    float:left;
    width:32px;
    height:100%;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
  }
  .rightBtn>span.help{
    background-image:url(../../static/img/study/help-light.png);
  }
  .rightBtn>span.skin{
    background-image:url(../../static/img/study/skin-light.png);
  }
  .rightBtn>span.close{
    background-image:url(../../static/img/study/close-light.png);
  }
  .rightBtn>span.help:hover{
    background-image:url(../../static/img/study/help-light-hover.png);
  }
  .rightBtn>span.skin:hover{
    background-image:url(../../static/img/study/skin-light-hover.png);
  }
  .rightBtn>span.close:hover{
    background-image:url(../../static/img/study/close-light-hover.png);
  }

  /**/
  .mainCon{
    margin: 60px 120px 0 120px;
    height:400px;
  }
  .mainCon .wordBox{
    width: 100%;
    min-height:50px;
    line-height:50px;
    font-family: "Arial";
    padding: 0 10px;
    box-sizing:border-box;
    font-size: 28px;
    word-wrap: break-word;
  }
  .mainCon .wordBox.wordBox1{
    /*color: #CD5C5C;*/
    color:rgb(217, 198, 161);
  }
  .mainCon .inputBox{
    position: relative;
    width:100%;
    height:50px;
    margin-bottom:8px;
  }
  .mainCon .inputBox input{
    border:none;
    outline: none;
    -webkit-appearance: none; -moz-appearance: none; -o-appearance: none; appearance: none;
    background-color: #c8c5b4;
    width:100%;
    height:100%;
    padding: 0 10px;
    box-sizing:border-box;
    font-size:28px;
    color:#333;
  }
  .mainCon .inputBox input.wrong {
    color: red;
  }
  .mainCon .inputBox .laba{
    position: absolute;
    top:0;
    left:-50px;
    width:50px;
    height:50px;
    line-height: 50px;
    color:#999;
    font-size:18px;
  }
  .mainCon .inputBox .isRight, .mainCon .inputBox .isWrong{
    position: absolute;
    top:0;
    right: -60px;
    width:50px;
    height:50px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size:40px 40px;
  }
  .mainCon .inputBox .isRight{
    background-image:url(../../static/img/study/spell_right.png);
  }
  .mainCon .inputBox .isWrong{
    background-image:url(../../static/img/study/spell_wrong.png);
  }
  .mainCon .inputBox.wrong  input{
    color:red;
  }
  .mainCon .mean{
    line-height: 50px;
    font-size: 24px;
    color:#666;
    overflow: hidden;
    margin:16px 0;
  }
  /**/
  .totalBar{
    width:100%;
    height:6px;
    background-color: #ccc;
    margin:0;
    position: relative;
  }
  .totalBar .curBar{
    position: absolute;
    top:0;left:0;
    width: 10%;
    height:6px;
    background: #0a724a;
  }
  /**/
  .botInfo{
    width:90%;
    margin:0 auto;
    color:#666;
    font-size:14px;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
  }
</style>



